// import linkArea from '../../components/linkArea/linkArea.vue'
import linkArea from '../../components/w-picker/w-picker.vue'
export default {
	components:{linkArea},
	data() {
		return {
			lotusAddressData:{
				visible:false,
				provinceName:'',
				cityName:'',
				townName:'',
			},
			region:'选择位置',
			form: {
				additionalProp1: '0',
				additionalProp2: '0',
				additionalProp3: '0',
				additionalProp4: '0',
				additionalProp5: '0',
				additionalProp6: '0',
				additionalProp7: '',
				additionalProp8: '',
				additionalProp9: '0',
				additionalProp10: '0',
				additionalProp11: '0'
			},
			list1: [
				{
					value: '0',
					name: '健康',
					checked: true
				},
				{
					value: '1',
					name: '有发烧、咳嗽等症状'
				},
				{
					value: '2',
					name: '其他症状'
				}
			],
			list2: [
				{
					value: '0',
					name: '无'
				},
				{
					value: '1',
					name: '有'
				}
			],
			tempList: [
				{
					value: '0',
					name: '37以下'
				},
				{
					value: '1',
					name: '37-38.5'
				},
				{
					value: '2',
					name: '38.5以上'
				}
			],
			yesOrNo: [
				{
					value: '0',
					name: '无'
				},
				{
					value: '1',
					name: '有'
				}
			],
			vehicleList: [
				{
					value: '0',
					name: '飞机'
				},
				{
					value: '1',
					name: '火车'
				},
				{
					value: '2',
					name: '汽车'
				},
				{
					value: '3',
					name: '轮船'
				}
			]
		};
	},
	computed:{
		text1(){
			let textArr= ['飞机-航班号','火车-车次','汽车-起止地点','轮船-起止地点']
			return this.form.additionalProp8?textArr[this.form.additionalProp8]:''
		},
		text2(){
			let textArr= ['飞机-起降地点','火车-乘车区间','汽车-是否司机']
			return this.form.additionalProp8 < 3?textArr[this.form.additionalProp8]:''
		},
		text3(){
			let textArr= ['飞机-座位号','火车-车厢及座位号']
			return this.form.additionalProp8 < 2?textArr[this.form.additionalProp8]:''
		},
	},
	onLoad(e) {},
	methods: {
		handleCancelChoose(){
			this.$refs.linkage.hide()
		},
		showChoose(){
			this.$refs.linkage.show()
		},
		handleChoose({checkArr,checkValue,defaultVal,result}){
			this.$refs.linkage.hide()
			this.region = result
		},
		choseValue(res){
			//res数据源包括已选省市区与省市区code
			console.log(res);
			this.lotusAddressData.visible = res.visible;//visible为显示与关闭组件标识true显示false隐藏
			//res.isChose = 1省市区已选 res.isChose = 0;未选
			if(res.isChose){
				this.lotusAddressData.provinceName = res.province;//省
				this.lotusAddressData.cityName = res.city;//市
				this.lotusAddressData.townName = res.town;//区
				this.region = `${res.province} ${res.city} ${res.town}`; //region为已选的省市区的值
			}
		},
		confirmCallback(){
			console.log(arguments,'参数')
			// let ads=this.$store.state.user_address;
		},
		handleRadioChange(evt, name) {
			this.form[name] = evt.target.value;
		},
		send() {
			let params = JSON.stringify({
				userid: '123',
				results: this.form
			});
			this.$HTTP({
				url: '/campusapp/userhealth/answer',
				root: 'http://61.132.95.169:10105',
				params,
				successCallback: res => {
					console.log(res, 'res');
					uni.showToast({
						title: '提交成功!'
					});
				}
			});
		}
	}
};
